@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Roboto', sans-serif}html,body{width:100%;height:100vh}html::-webkit-scrollbar,body::-webkit-scrollbar{width:7px}html::-webkit-scrollbar-track,body::-webkit-scrollbar-track{width:7px;background:#111212}html::-webkit-scrollbar-thumb,body::-webkit-scrollbar-thumb{background:#18ff9f;border-radius:.2em}

.main{background:#111212;width:100%;height:300vh}.main__landing{height:100vh}.main__landing__content{display:grid;grid-template-columns:90% 10%;grid-template-rows:1fr;position:absolute;top:0;left:0;width:100%;height:100vh}.main__landing__person{display:flex;justify-content:center;align-items:flex-start;flex-direction:column;padding:0 4em;background:#0000005b}.main__landing__hello{font-size:2.5rem;color:#f3f3f3;font-weight:lighter}.main__landing__fullname{color:#18ff9f;font-size:3rem;font-weight:lighter}.main__landing__video{-webkit-filter:brightness(70%);filter:brightness(70%);position:absolute;top:0;left:0;object-fit:cover;width:100%;height:100vh;z-index:0}.main__about{padding:0 4em;height:100vh;display:flex;flex-direction:row;justify-content:space-between;align-items:center;background:#131515}.main__about__description{color:#788282;height:50%}.main__about__selfie{margin:0 2em 0 0;height:50%;border-radius:.2em}

.navigation{background:#111212}.navigation__links{display:flex;align-items:center;justify-content:space-evenly;flex-direction:column;list-style-type:none;height:100%}.navigation__link{display:flex;align-items:center;color:#1C1F1F;text-transform:uppercase;font-size:.9rem;-webkit-transition:color .3s ease-in-out;transition:color .3s ease-in-out;cursor:pointer}.navigation__link:hover{color:#18ff9f}.navigation__link--selected{color:#18ff9f}.navigation__link svg{margin:0 5px 0 0}

